
/* RESET */
/* ----------------------------------------- */

/* Global reset
 Based upon 'reset.css' in the Yahoo! User Interface Library: http://developer.yahoo.com/yui
*/

*
, html
, body
, th
, td
{
	margin: 0;
	padding: 0;
}

table
{
	border-collapse: collapse;
	border-spacing: 0;
}

fieldset
, img
{
	border: 0;
}

address
, caption
, cite
, code
, dfn
, em
, strong
, th
, var
{
	font-style: normal;
	font-weight: normal;
}

ol
, ul
, li
{
	list-style: none;
}

caption
, th
{
	text-align: left;
}

h1
, h2
, h3
, h4
, h5
, h6
{
	font-size: 100%;
	font-weight: normal;
}

q:before
, q:after
{
	content: '';
}

/* Restores some sensible defaults */
strong
{
	font-weight: 700;
}

em
{
	font-style: italic;
}

a img
{
	border: 0; /* Removes IE's blue borders */
}



@-webkit-keyframes fullRotate
{
	0%
	{
		-webkit-transform: rotate( 0deg );
		        transform: rotate( 0deg );
	}

	100%
	{
		-webkit-transform: rotate( 360deg );
		        transform: rotate( 360deg );
	}
}
@keyframes fullRotate
{
	0%
	{
		-webkit-transform: rotate( 0deg );
		        transform: rotate( 0deg );
	}

	100%
	{
		-webkit-transform: rotate( 360deg );
		        transform: rotate( 360deg );
	}
}

:root {
	--white: #fff;
	--black: #000;
	--black-alpha: rgba(0,0,0,.8);
	--gray: #666;
	--gray-dark: #222;
	--gray-light: #e6e6e6;
	--gray-lightest: #f6f6f6;
	--red: #ff473b;
	--red-dark: #ac291e;
	--red-alpha: rgba(255,71,59,.93);
	--red-alpha-light: rgba(255,71,59,.1);
}

*
, *::before
, *::after
{
	box-sizing: border-box;
}

* + *
{
	margin-top: 1em;
}

body
, button
, html
, input
, select
, table
, td
, textarea
{
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
}

html
{
	-webkit-text-size-adjust: 100%;
}

body
{
	margin: 0 auto;
	padding: 0;
	line-height: 1.7;
}

ul
{
	list-style-type: none;
}

ul li
{
	margin-left: 1em;
	list-style: square;
}

ol li
{
	list-style: decimal;
}

h1 a:link, h1 a:visited
, h2 a:link, h2 a:visited
, h3 a:link, h3 a:visited
, h4 a:link, h4 a:visited
, h5 a:link, h5 a:visited
, h6 a:link, h6 a:visited
{
	font-weight: inherit;
	color: inherit;
}

table
{
	width: 100%;
	border: 0;
}

a
{
	color: var(--red);
	transition: color .3s ease;
}

a:hover
{
	color: var(--gray);
}

abbr
{
	font-size: .75em;
	text-transform: uppercase;
}

label
{
	font-weight: 700;
}

input[ type="email" ]
, input[ type="number" ]
, input[ type="search" ]
, input[ type="submit" ]
, input[ type="tel" ]
, input[ type="text" ]
, input[ type="url" ]
, textarea
{
	width: 100%;
	height: auto;
	margin: 0;
	padding: .25em .5em;
	font-family: inherit;
	font-size: 100%;
	line-height: 1.7;
}

input[ type="submit" ]
, button[ type="submit" ]
{
	-webkit-appearance: none;
}

input[ type="file" ]
{
	padding: .25em .5em .75em;
	line-height: 0;
	border: 0;
}

img
{
	max-width: 100%;
	height: auto;
	display: block;
}

select
{
	width: 100%;
}

iframe
{
	max-width: 100%;
	height: auto;
}

pre
{
	border-radius: 4px;
	-moz-tab-size: 2;
	     tab-size: 2;
}

code
{
	font-size: 1em;
}

