/** VARIABLES
===================================*/
/** RESET AND LAYOUT
===================================*/
.bx-wrapper {
  position: relative;
  margin-bottom: 60px;
  padding: 0;
  *zoom: 1;
  touch-action: pan-y;
}
.bx-wrapper img {
  max-width: 100%;
  display: block;
}
.bxslider {
  margin: 0;
  padding: 0;
  /*fix flickering when used background-image instead of <img> (on Chrome)*/
  -webkit-perspective: 1000;
}
ul.bxslider {
  list-style: none;
}
.bx-viewport {
  /* fix other elements on the page moving (in Chrome) */
  -webkit-transform: translateZ(0);
}
/** THEME
===================================*/
.bx-wrapper {
  box-shadow: 0 0 5px #ccc;
  border: 5px solid #fff;
  background: #fff;
}
.bx-wrapper .bx-pager,
.bx-wrapper .bx-controls-auto {
  position: absolute;
  bottom: -30px;
  width: 100%;
}
/* LOADER */
.bx-wrapper .bx-loading {
  min-height: 50px;
  background: url(data:image/gif;base64,) center center no-repeat #ffffff;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2000;
}
/* PAGER */
.bx-wrapper .bx-pager {
  text-align: center;
  font-size: .85em;
  font-family: Arial;
  font-weight: bold;
  color: #666;
  padding-top: 20px;
}
.bx-wrapper .bx-pager.bx-default-pager a {
  background: #666;
  text-indent: -9999px;
  display: block;
  width: 10px;
  height: 10px;
  margin: 0 5px;
  outline: 0;
  border-radius: 5px;
}
.bx-wrapper .bx-pager.bx-default-pager a:hover,
.bx-wrapper .bx-pager.bx-default-pager a.active,
.bx-wrapper .bx-pager.bx-default-pager a:focus {
  background: #000;
}
.bx-wrapper .bx-pager-item,
.bx-wrapper .bx-controls-auto .bx-controls-auto-item {
  display: inline-block;
  vertical-align: bottom;
  *zoom: 1;
  *display: inline;
}
.bx-wrapper .bx-pager-item {
  font-size: 0;
  line-height: 0;
}
/* DIRECTION CONTROLS (NEXT / PREV) */
.bx-wrapper .bx-prev {
  left: 10px;
  background: url(data:image/png;base64,) no-repeat 0 -32px;
}
.bx-wrapper .bx-prev:hover,
.bx-wrapper .bx-prev:focus {
  background-position: 0 0;
}
.bx-wrapper .bx-next {
  right: 10px;
  background: url(data:image/png;base64,) no-repeat -43px -32px;
}
.bx-wrapper .bx-next:hover,
.bx-wrapper .bx-next:focus {
  background-position: -43px 0;
}
.bx-wrapper .bx-controls-direction a {
  position: absolute;
  top: 50%;
  margin-top: -16px;
  outline: 0;
  width: 32px;
  height: 32px;
  text-indent: -9999px;
  z-index: 10;
}
.bx-wrapper .bx-controls-direction a.disabled {
  display: none;
}
/* AUTO CONTROLS (START / STOP) */
.bx-wrapper .bx-controls-auto {
  text-align: center;
}
.bx-wrapper .bx-controls-auto .bx-start {
  display: block;
  text-indent: -9999px;
  width: 10px;
  height: 11px;
  outline: 0;
  background: url(data:image/png;base64,) -86px -11px no-repeat;
  margin: 0 3px;
}
.bx-wrapper .bx-controls-auto .bx-start:hover,
.bx-wrapper .bx-controls-auto .bx-start.active,
.bx-wrapper .bx-controls-auto .bx-start:focus {
  background-position: -86px 0;
}
.bx-wrapper .bx-controls-auto .bx-stop {
  display: block;
  text-indent: -9999px;
  width: 9px;
  height: 11px;
  outline: 0;
  background: url(data:image/png;base64,) -86px -44px no-repeat;
  margin: 0 3px;
}
.bx-wrapper .bx-controls-auto .bx-stop:hover,
.bx-wrapper .bx-controls-auto .bx-stop.active,
.bx-wrapper .bx-controls-auto .bx-stop:focus {
  background-position: -86px -33px;
}
/* PAGER WITH AUTO-CONTROLS HYBRID LAYOUT */
.bx-wrapper .bx-controls.bx-has-controls-auto.bx-has-pager .bx-pager {
  text-align: left;
  width: 80%;
}
.bx-wrapper .bx-controls.bx-has-controls-auto.bx-has-pager .bx-controls-auto {
  right: 0;
  width: 35px;
}
/* IMAGE CAPTIONS */
.bx-wrapper .bx-caption {
  position: absolute;
  bottom: 0;
  left: 0;
  background: #666;
  background: rgba(80, 80, 80, 0.75);
  width: 100%;
}
.bx-wrapper .bx-caption span {
  color: #fff;
  font-family: Arial;
  display: block;
  font-size: .85em;
  padding: 10px;
}

.Loader
{
	position: relative;
	display: inline-block;
	overflow: hidden;
}

.Loader__icon
, .Loader__icon::after
{
	border-radius: 50%;
	width: 4em;
	height: 4em;
}

.Loader__icon
{
	position: relative;
	text-indent: -9999em;
	border-top: .5em solid currentColor;
	border-right: .5em solid currentColor;
	border-bottom: .5em solid currentColor;
	border-left: .5em solid var(--red);
	-webkit-transform: translateZ( 0 );
	        transform: translateZ( 0 );
	color: var(--gray-light);
	-webkit-animation: fullRotate 1.25s infinite linear;
	        animation: fullRotate 1.25s infinite linear;
}

.Message
{
	padding: .75em 1em;
	border-width: 1px;
	border-style: solid;
	border-radius: 4px;
	border-color: var(--red);
	background-color: var(--red-trans-light);
	color: var(--red);
}

.Modal
{
	margin-top: 0;
	padding: 3em 1.5em;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 50;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	opacity: 0;
	visibility: hidden;
	pointer-events: none;
	transition: opacity .4s;
}

.Modal::before
{
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	content: '';
	flex: 1 1 100%;
	background-color: var(--black-alpha);
}

.Modal.is-active
{
	opacity: 1;
	visibility: visible;
	pointer-events: auto;
}

.Modal .Container
{
	max-width: 40rem;
	position: relative;
}

.Modal__content
{
	padding: 2rem;
	background-color: var(--white);
}

.Modal__button
{
	width: 1.5rem;
	height: 1.5rem;
	display: block;
	padding: 1em;
	position: absolute;
	bottom: calc( 100% + .5rem );
	right: 0;
	font-size: 0;
	border: 0;
	background-color: transparent;
	outline: 0;
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
}

.Modal__button:hover::after
{
	color: var(--red);
}

.Modal__button::after
{
	content: '\2715';
	display: block;
	font-size: 1.5rem;
	color: var(--white);
	transition: color .3s;
}

.Nav
{
	background: var(--gray-lightest);
	display: flex;
}

.Nav__item
{
	margin: 0;
	list-style: none;
	position: relative;
}

.Nav__item a
{
	padding: .5em 1.5em;
	display: block;
	text-decoration: none;
	color: var(--gray);
	border-right: 1px solid var(--gray-light);
}

.Nav__item:hover > a
, .Nav__item.is-active > a
{
	background-color: var(--red);
	color: var(--white);
}

.Nav__item--sub > a::after
{
	width: 0;
	height: 0;
	margin-left: .5em;
	content: '';
	display: inline-block;
	vertical-align: .1em;
	border-top: .375em solid;
	border-right: .375em solid transparent;
	border-bottom: 0;
	border-left: .375em solid transparent;
	transition: -webkit-transform .3s ease;
	transition: transform .3s ease;
	transition: transform .3s ease, -webkit-transform .3s ease;
}

.Nav__item--sub:hover .Nav__sub
{
	display: block;
}

/* Nav Sub */
.Nav__sub
{
	width: 12em;
	margin-top: 0;
	position: absolute;
	left: 0;
	display: none;
	background-color: var(--gray-light);
}

.Nav__sub .Nav__item
{
	margin: 0;
	display: block;
	list-style: none;
}


.Select
{
	max-width: 16em;
	padding: .325em 1em;
	font-size: 1em;
	border: 1px solid;
	outline: none;
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	background-image: url(/lexicon/static/media/caret.e143df2e.svg);
	background-size: 12px 6px;
	background-repeat: no-repeat;
	background-position: right 12px center;
	transition: background-color .25s ease;
}

.Select:hover
{
	background-color: var(--gray-light);
}

.Select:focus
{
	box-shadow: 0 0 3px var(--gray);
}

.Slider * + *
, .bx-controls
, .bx-controls * + *
{
	margin-top: 0;
}

.Snackbar
{
	width: 100%;
	margin: 0;
	padding: 1em;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 99;
	text-align: center;
	background-color: var(--red);
	color: var(--white);
	-webkit-transform: translateY(-100%);
	        transform: translateY(-100%);
	opacity: 0;
	transition: opacity .2s ease, background-color .3s ease, -webkit-transform .2s ease;
	transition: opacity .2s ease, transform .2s ease, background-color .3s ease;
	transition: opacity .2s ease, transform .2s ease, background-color .3s ease, -webkit-transform .2s ease;
	cursor: pointer;
}

.Snackbar:hover
{
	background-color: var(--gray-dark);
}

.Snackbar.is-active
{
	-webkit-transform: translateY(0);
	        transform: translateY(0);
	opacity: 1;
}

.StickyExample
{
	height: 25em;
	overflow-y: auto;
	position: relative;
}

.Sticky
{
	width: 100%;
	height: 4rem;
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	z-index: 10;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: var(--red);
	color: var(--white);
}

.StickyExample .Content
{
	padding-top: 4rem;
	position: absolute;
	top: 0;
}

.Switch
{
	width: 4rem;
	height: 2rem;
	position: relative;
	display: inline-block;
}

.Switch input
{
	display: none;
}

.Switch__handle
{
	margin-top: 0;
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	border-radius: 2rem;
	background-color: var(--gray-light);
	transition: .4s;
	cursor: pointer;
}

.Switch__handle::before
{
	width: 2rem;
	height: 2rem;
	position: absolute;
	bottom: 0;
	left: 0;
	content: '';
	border: 4px solid var(--gray-light);
	border-radius: 50%;
	background-color: var(--gray-lightest);
	transition: .4s;
}

input:checked + .Switch__handle
{
	background-color: var(--red);
}

input:focus + .Switch__handle
{
	box-shadow: 0 0 1px var(--red);
}

input:checked + .Switch__handle::before
{
	border: 4px solid var(--red);
	-webkit-transform: translateX(100%);
	        transform: translateX(100%);
}

.TabGroup
{
	width: 100%;
	display: flex;
	border-bottom: 1px solid var(--gray-light);
}

.Tab
{
	margin: 0 0 -1px;
	display: inline-block;
	border: 1px solid transparent;
	border-top-left-radius: 4px;
	border-top-right-radius: 4px;
	transition: background-color .3s ease, border-color .3s ease, color .3s ease;
}

.Tab a
{
