	padding: 0;
	display: flex;
	border: 1px solid var(--gray-light);
	height: 20em;
}

.TagalongExample__sidebar
{
	flex: 0 1 20%;
	padding: 1.5em 0;
	border-right: 1px solid var(--gray-light);
}

.TagalongExample__content
{
	margin-top: 0;
	padding: 1.5em 2em 10em;
	flex: 0 1 80%;
	overflow-y: auto;
}

/* Tagalong */
.Tagalong__item
{
	margin: 0;
	display: block;
}

.Tagalong__item a
{
	display: block;
	text-decoration: none;
	padding: .125em 1.5em;
	color: var(--gray);
	font-size: .875em;
	transition: background-color .3s ease, color .3s ease;
}

.Tagalong__item a:hover
, .Tagalong__item.is-active a
{
	background-color: var(--red);
	color: var(--white);
}

.TitleExample h1
, .TitleExample h2
, .TitleExample h3
, .TitleExample h4
, .TitleExample h5
, .TitleExample h6
{
	font-size: 1em;
	line-height: normal;
}

.TitleExample p:nth-of-type(n+2)
{
	margin-top: 2em;
}

.TitleExample .Title--1
{
	font-size: 1.5em;
	font-weight: 700;
	line-height: 1;
	margin-top: 1rem;
	color: var(--red);
}

.Tooltip
{
	position: relative;
	display: inline-block;
	border-bottom: 2px dotted var(--gray);
}

.Tooltip .Tooltip__content
{
	padding: .25em 1.5em;
	position: absolute;
	bottom: 125%;
	left: 50%;
	z-index: 1;
	white-space: nowrap;
	border-radius: 3px;
	background-color: var(--red-alpha);
	color: var(--white);
	visibility: hidden;
	opacity: 0;
	-webkit-transform: translate( -50%, 25% );
	        transform: translate( -50%, 25% );
	transition: opacity .3s ease, -webkit-transform .3s ease;
	transition: opacity .3s ease, transform .3s ease;
	transition: opacity .3s ease, transform .3s ease, -webkit-transform .3s ease;
}

.Tooltip .Tooltip__content::after
{
	margin-left: -5px;
	position: absolute;
	top: 100%;
	left: 50%;
	content: '';
	border-width: 5px;
	border-style: solid;
	border-color: var(--red-alpha) transparent transparent transparent;
}

.Tooltip:hover .Tooltip__content
{
	-webkit-transform: translate( -50%, 0 );
	        transform: translate( -50%, 0 );
	visibility: visible;
	opacity: 1;
}

.Wrap
{
	padding: 3rem;
	background-color: var(--red);
}

.Wrap .Container
{
	height: 5rem;
	padding: 1rem;
	background-color: var(--white);
}



.Drawer__button
{
	width: 100%;
	padding: 1em 2em;
	font-size: 1em;
	text-align: left;
	border: 0;
	background-color: var(--red);
	color: var(--white);
	transition: background-color .3s ease, color .3s ease;
}

.Drawer__button:focus,
.Drawer__button:hover
{
	background-color: var(--gray-dark);
}

.Drawer__button::after
{
	width: 0;
	height: 0;
	margin-left: .5em;
	content: '';
	display: inline-block;
	vertical-align: middle;
	border-top: .375em solid;
	border-right: .375em solid transparent;
	border-bottom: 0;
	border-left: .375em solid transparent;
	-webkit-transform: rotate(-90deg) translateX(.1em);
	        transform: rotate(-90deg) translateX(.1em);
	-webkit-transform-origin: center;
	        transform-origin: center;
	transition: -webkit-transform .3s ease;
	transition: transform .3s ease;
	transition: transform .3s ease, -webkit-transform .3s ease;
}

.Drawer__holder
{
	margin-top: 0;
	max-height: 0;
	border: 1px solid var(--gray-lightest);
	overflow: hidden;
	transition: max-height .3s ease;
}

.Drawer__content
{
	margin-top: 0;
	padding: 2em;
}

.Drawer.is-active .Drawer__holder
{
	max-height: 15rem;
}

.Drawer.is-active .Drawer__button::after
{
	-webkit-transform: rotate( 0 );
	        transform: rotate( 0 );
}

